.TopNavBarCont {
  width: 100%;
  height: fit-content;

  color: white;

  top: 0;
  left: 0;
  margin: 0;
  padding: 0;
}

.brand {
  padding-left: 15px;
  padding-top: 15px;
  padding-bottom: 15px;
}
.links {
  padding-left: 5px;
  text-align: center;
  /* height: 100px; */
}
.smallrows {
  height: 50px;
}

.linksSmall {
  padding-left: 25%;
  text-align: left;
}

.image {
  padding-left: 5px;
  width: 25px;
  height: 25px;
}
.collapseButton {
  width: 25px;
  height: 25px;
}

table {
  width: 100%;
  height: 30px;
  text-align: left;
  background-color: #24344d;
  border-collapse: collapse;
  border-spacing: 0;
}

tr,
td {
  text-align: left;
}

.navlink {
  text-decoration: none;
  color: white;
}
